.NavBody {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  height: 50px;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out;
}

.NavBody.scrolled {
  background-color: white;
}

.SideNav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 5px;
}

.leftElement {
  display: flex;
  padding-left: 20px;
}

.rightElement {
  display: flex;
  padding-right: 20px;
}

.Title {
  padding-left: 20px;
  padding-top: 5px;
  font-size: 24px; /* 반응형으로 폰트 크기 조정 */
}

.Signin{ /* 반응형으로 간격 조정 */
  padding-top: 5px;
}

.Login {
  padding-right: 30px; /* 반응형으로 간격 조정 */
  padding-top: 5px;
  font-size: 20px; /* 반응형으로 폰트 크기 조정 */
}

Sidebar {
    width: 320;
  }

/* 반응형 웹 CSS 추가 */
@media (max-width: 768px) {
  /* 화면 크기가 768px 이하일 때 적용되는 스타일 */
  
  Sidebar {
    width: 240;
  }
}
