
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
}
.loginModal {
  width: 480px;
  height: 621px;
  background-color: white;
  position: relative;
  box-sizing: border-box;
  margin: 150px auto;
  padding: 20px;
  background: #fff;
  border-radius:10px;
}
.close {
  float: right;
  font-size: 40px;
}
.modalContents {
  margin: 0 auto;
  width: 100%;
  position: relative;
  padding: 0 20px 32px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.signinIcon {
  width: 150px;
  margin: 0 auto;
}

.loginBtn {
  height: 40px;
  font-size: 14px;
  padding: 13px 30px;
  cursor: pointer;
  background-color: black;
  color: white;
  line-height: 1px;
  margin-top: 20px;
  margin-bottom: 12px;
  border-radius: 3px;
  border-style: none;
}
.title {
  text-align: center;
  padding: 70px;
  font-size: 50px;
  font-weight: bold;
}

.socialBox {
  margin-bottom: 30px;
}
.kakao {
  background-color: #feec34;
  border-color: #feec34;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin-bottom: 10px;
  border-radius: 20px;
  cursor: pointer;
}
.kakaoLogo {
  width: 24px;
  height: 25px;
}
.kakaoText {
  width: 300px;
  font-size: 15px;
  text-align: center;
  display: inline-block;
  box-sizing: border-box;
}

.facebook {
  background-color: #21538a;
  border-color: #21538a;
  height: 40px;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  color: #fff;
  border-radius: 3px;
}
.facebookText {
  padding-top: 12px;
  width: 310px;
  color: #fff;
  font-size: 15px;
  text-align: center;
  box-sizing: border-box;
}
.facebookLogo {
  padding-top: 7px;
  width: 24px;
  height: 25px;
}
        
      
.loginEnd {
  text-align: center;
  font-size: 11px;
}
.loginLine {
  color: #bcbcbc;
  font-size: 11px;
  margin-bottom: 35px;}
.loginLine a {
  color: black;
  text-decoration: underline;
  cursor: pointer;
}

.noUser {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .loginModal {
    width: 90%; /* 예시로 90%로 조정합니다. 필요에 따라 조절하세요. */
    height: auto; /* 높이를 자동으로 조정하여 내용에 따라 늘어나게 합니다. */
    margin: 160px auto; /* 모바일 화면에서 더 중앙에 위치하도록 조정합니다. */
  }
  .title {
    font-size: 30px; /* 모바일 화면에서 제목 폰트 크기를 줄입니다. */
    padding: 40px; /* 모바일 화면에서 제목과 모달 사이의 간격을 조정합니다. */
  }
  .kakaoText {
    width: 170px;
  }
}